.apps-content {
    border-left: 1px solid #698878d5;
    border-right: 1px solid #698878d5;
    border-bottom: 1px solid #698878d5;
    background: white;
}

@media only screen and (min-width: 0px) and (max-width: 399px) {
    img {
      width: 165px;
      height: 165px;
      background-size: 100%, 100%;
      border: 1px solid gray;
    }
  }
  
  @media only screen and (min-width: 400px) and (max-width: 434px) {
    img {
      width: 165px;
      height: 165px;
      background-size: 100%, 100%;
      border: 1px solid gray;
    }
  }
  
  @media only screen and (min-width: 435px) and (max-width: 565px) {
    img {
      width: 165px;
